import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userName: "",
    userId: "",
    onload: false,
    loading: true,
    token: "",
    questionnaireObj: {
      name: "问卷名称",
      interpretation: "问卷说明",
      idForQues: 1,
      totalPage: 1,
      data: [],
    },
  },
  mutations: {
    setToken(state, newToken) {
      state.token = newToken;
    },
    emitQues(state, dataObj) {
      state.questionnaireObj = JSON.parse(JSON.stringify(dataObj));
    },
  },
  getters: {
    Token: (state) => {
      return state.token;
    },
  },
  actions: {},
  modules: {},
});
