import request from '../utils/request'
//接口在这个文件下写

// 获取某个问卷的所有信息
export function getVote(params, voteId) {
    return request({
        url: `/get/votes?${voteId}`,
        method: 'get',
        params
    })
}

// 获取当前登录用户发布的所有投票的信息
export function myVote(data) {
    return request({
        url: `/my`,
        method: 'get',
        data
    })
}

// 创建一个文件
export function addVote(data) {
    return request({
        url: `/add/votes`,
        method: 'post',
        data
    })
}

// 删除一个文件
export function deleteVote(data) {
    return request({
        url: `/delete/votes`,
        method: 'post',
        data
    })
}

// 提交问卷
export function submitAnswer(data) {
    return request({
        url: `/answers`,
        method: 'post',
        data
    })
}

// 登录
export function login(data) {
    return request({
        url: `/login`,
        method: 'post',
        data
    })
}