import axios from 'axios'
import { Message } from 'element-ui'
import store from '../store'
import { getToken } from './auth'
import qs from 'querystring';
import { toLogin } from "../router";
// import { toError } from "../router"

axios.defaults.withCredentials = true;

const service = axios.create({
    baseURL: '/api',
    transformRequest: [(oldData, config) => {
        if (!config['Content-Type']) return qs.stringify(oldData);
        switch (config['Content-Type']) {
            case 'multipart/form-data':
                return oldData;
            default:
                return qs.stringify(oldData)
        }
    }],
    timeout: 60000
});

service.interceptors.request.use(config => {
    if (store.getters.Token) {
        window.console.log(store.getters.Token)
        config.headers['token'] = getToken();
        // config.headers['token'] = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJhdWQiOlsiMzAxOTI0NDM5NSIsIjEiXSwiZXhwIjoxNjM3OTk4NjQ4fQ.h4W7MEechz1L0MNEbDbkb9-PvhCgVYUCAt174pd5TEk';
        config.headers['Access-Control-Allow-Origin'] = true;
        // config.headers['Content-Type'] = 'multipart/form-data';

    } else {
        // config.headers['token'] = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJhdWQiOlsiMzAxOTI0NDM5NSIsIjEiXSwiZXhwIjoxNjM3OTk4NjQ4fQ.h4W7MEechz1L0MNEbDbkb9-PvhCgVYUCAt174pd5TEk';
    }
    return config
}, error => {
    return Promise.reject(error);
});

service.interceptors.response.use(response => {
        if (response.status === 0) {
            if (response.request.responseType === 'blob') {
                return response.data
            }
            const data = response.data;
            switch (data["code"]) {
                case 0:
                    return data;
                case 30001:
                    Message({
                        message: `问卷问题json解析错误`
                    });
                    return Promise.reject(data['message']);
                case 40001:
                    toLogin();
                    Message({
                        message: `${data["message"]},请重新登陆`,
                        type: "error",
                        duration: 5000
                    });
                    return Promise.reject(data["message"]);
                case 40002:
                    Message({
                        message: '用户不存在，登陆失败',
                        type: "error",
                        duration: 5000
                    });
                    return Promise.reject(data["message"]);
                case 40003:
                    Message({
                        message: '账号密码错误，登陆失败',
                        type: "error",
                        duration: 5000
                    });
                    return Promise.reject(data["message"]);
                default:
                    Message({
                        message: data["message"],
                        type: "error",
                        duration: 5000
                    });
                    return data;
                    //return Promise.reject(data["msg"])
            }
        } else if (response.status === 500) {
            Message({
                message: "系统错误，请联系管理员修复",
                type: "error",
                duration: 5000
            });
        }
        return response
    },
    error => {
        if (error.message === "Network Error") {
            // toError();
        } else {
            Message({
                message: error.message,
                type: 'error',
                duration: 5000
            });
        }

        return Promise.reject(error)
    });

export default service